import React from 'react'
import ComposedTextField from '../../Component/textFields'
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import history from '../../history'
import { login } from '../../Config/Functions/Functions'
import Appbar from '../../Component/loginbar'
import {firebaseApp} from '../../Config/Firebase/Firebase'

class Login extends React.Component {
    constructor(){
        super();
        this.state = {
          email:'',
          password:''
        }
        this.checkOut = this.checkOut.bind(this)
      }
      componentDidMount(){
        firebaseApp.auth().onAuthStateChanged(function(user) {
            if (user) {
                this.props.history.push("/Dashboard")
            } else {
                this.props.history.push("/")
            }
          });
      }
      
      async checkOut(){
        console.log(this.state);
        try{
          let user = await login(this.state);
          console.log(user);
          this.props.history.push("/Dashboard")
        //   history.push('/Dashboard',{value:this.props.location.state.value});
        }catch(err){
          console.log(err.message)
        }
        }
    render() {
        return (
            <div>
                <Appbar />
                <Grid justify='center' container spacing={3}>
                    <Grid item xs={12} sm={11} md={10} lg={7}>
                        <Paper>
                            <div >
                                <br /><br />
                                <div style={{ textAlign: "center" }}>
                                    <b style={{ fontSize: '2.5em' }}>Admin Login</b>
                                </div>
                                <div style={{ textAlign: "center" }}>
                                    <ComposedTextField onchange={(e) => { this.setState({ email: e.target.value }) }} type="email" style={{ width: '65%' }} name="email" />
                                    <ComposedTextField onchange={(e) => { this.setState({ password: e.target.value }) }} type="password" style={{ width: '65%' }} name="password" />
                                    <Button size="large" onClick={this.checkOut} style={{ color: "#fff", backgroundColor: "#6cb860", marginTop: "10px",width:'150px' }}>
                                        Login
                                    </Button>
                                    <br />
                                </div>
                                <br /><br />
                            </div>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
        )
    }
}

export default Login