import React from 'react'
import Drawer from '../../Component/Drawer'
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import {firebaseApp} from '../../Config/Firebase/Firebase'

class Home extends React.Component{
    constructor() {
        super()
        this.state = {
            count: 0,
            myCart: [],
            products: [],
            orders: [],
            users : []
        }
    }
    componentDidMount() {
        let myLocalCart = localStorage.getItem("cartDetail")
        if (myLocalCart) {
            this.setState({
                myCart: JSON.parse(myLocalCart)
            })
        }

        let { products, orders, users } = this.state
        firebaseApp.firestore().collection("All_Products").get()
            .then((snapshot) => {
                snapshot.forEach(doc => {
                    let getproducts = doc.data()
                    products.push(getproducts)
                    this.setState({
                        products: products
                    })
                })
        })

        firebaseApp.firestore().collection("Orders").get()
        .then((snapshot) => {
            snapshot.forEach(doc => {
                let getorders = doc.data()
                orders.push(getorders)
                this.setState({
                    orders: orders
                })
            })
    })
    }
    render(){
        let { products , orders, users} = this.state;
        let value=[]
        return(
            <div>
                <Drawer value={this.state.myCart} />
                <div style={{marginTop:'10%'}}>
                <Grid justify='center' container spacing={3}>
                    <Grid item xs={11} sm={10} md={8} lg={5}>
                        <Paper onClick={()=>{this.props.history.push('AllProduct',{value})}} style={{cursor:'pointer',padding:20}}>
                            <Typography variant='h4' align='center' >Total Products</Typography>
                            <Typography variant='h5' align='center' >{products.length}</Typography>
                        </Paper>
                    </Grid>
                    <Grid item xs={11} sm={10} md={8} lg={5}>
                        <Paper onClick={()=>{this.props.history.push('SellDetail',{value})}} style={{cursor:'pointer',padding:20}}>
                            <Typography variant='h4' align='center' >Total Sell</Typography>
                            <Typography variant='h5' align='center' >{orders.length}</Typography>
                        </Paper>
                    </Grid>
                </Grid>
            </div>
            </div> 
        )
    }
}

export default Home