import React from "react"
import {Router, Route} from "react-router-dom";
import {Home, Login, AddProduct, AllProduct, DeleteProduct,CartValue,SellDetail,editproduct} from '../../Containers/index'
import history from '../../history'

export default class AppRouter extends React.Component{
    render(){
        return(
            <Router history={history}>
                <Route exact path="/" component={Login} />
                <Route  path="/Dashboard" component={Home} />
                <Route  path="/AddProduct" component={AddProduct} />
                <Route  path="/AllProduct" component={AllProduct} />
                <Route  path="/DeleteProduct" component={DeleteProduct} />
                <Route  path="/CartDetail" component={CartValue} />
                <Route  path="/SellDetail" component={SellDetail} />
                <Route  path="/EditProduct" component={editproduct} />
            </Router>
            
        )
    }
}

